import { ActionRecipeSpecification } from '@backbase/data-ang/actions';
import { createAction, props } from '@ngrx/store';
import { LoadSpecificationsPayload } from '../model/action-recipes-actions.model';

export const loadSpecifications = createAction(
  '[ACT Specifications] Load Specifications',
  props<LoadSpecificationsPayload>(),
);

export const loadSpecificationsSuccess = createAction(
  '[ACT Specifications] Load Specifications Success',
  props<{ specifications: ActionRecipeSpecification[] }>(),
);

export const loadSpecificationsFailed = createAction(
  '[ACT Specifications] Load Specifications Failed',
  props<{ error: boolean }>(),
);
