import { Injectable } from '@angular/core';
import { ActionRecipeItem, SpecificationsWithAction } from '@backbase/actions-common-ang';
import { Store } from '@ngrx/store';
import { loadActionRecipes, saveActionRecipe } from './action-recipes/actions';
import { loadSpecifications } from './specifications/actions';
import { specificationsError, specificationsLoading, specifications } from './specifications/selectors';
import { actionRecipes, actionRecipesError, actionRecipesLoading } from './action-recipes/selectors';
import { specificationsWithActionRecipe } from './selectors';
import {
  LoadActionRecipesPayload,
  LoadSpecificationsPayload,
  SaveActionRecipePayload,
} from './model/action-recipes-actions.model';
import { ActionsState } from './model/actions-store-state.model';
import { Observable } from 'rxjs';
import { ActionRecipeSpecification } from '@backbase/data-ang/actions';

@Injectable()
export class ActionsStoreModel {
  constructor(private readonly store: Store<ActionsState>) {}
  /**
   * Stream of specifications.
   */
  readonly specifications$: Observable<ActionRecipeSpecification[]> = this.store.select(specifications);

  /**
   * Stream of specifications loading flags.
   */
  readonly specificationsLoading$: Observable<boolean> = this.store.select(specificationsLoading);

  /**
   * Stream of specifications errors.
   */
  readonly specificationsError$: Observable<boolean> = this.store.select(specificationsError);

  /**
   * Stream of action recipes.
   */
  readonly actionRecipes$: Observable<ActionRecipeItem[]> = this.store.select(actionRecipes);

  /**
   * Stream of action recipes loading flags.
   */
  readonly actionRecipesLoading$: Observable<boolean> = this.store.select(actionRecipesLoading);

  /**
   * Stream of action recipes errors.
   */
  readonly actionRecipesError$: Observable<Error | undefined> = this.store.select(actionRecipesError);

  /**
   * Stream of specifications with action recipe.
   */
  readonly specificationsWithActionRecipe$: Observable<SpecificationsWithAction> =
    this.store.select(specificationsWithActionRecipe);

  /**
   * Method to dispatch load specifications.
   *
   * @param payload - load specifications payload
   */
  dispatchLoadSpecifications(payload: LoadSpecificationsPayload) {
    this.store.dispatch(loadSpecifications(payload));
  }

  /**
   * Method to dispatch load action recipes.
   *
   * @param payload - load specifications payload
   */
  dispatchLoadActionRecipes(payload: LoadActionRecipesPayload) {
    this.store.dispatch(loadActionRecipes(payload));
  }

  /**
   * Method to dispatch save action recipe.
   *
   * @param payload - action recipe payload
   */
  dispatchSaveActionRecipe(payload: SaveActionRecipePayload) {
    this.store.dispatch(saveActionRecipe(payload));
  }
}
