import { Action, createReducer, on } from '@ngrx/store';
import {
  loadActionRecipes,
  loadActionRecipesFailed,
  loadActionRecipesSuccess,
  saveActionRecipeFailure,
  saveActionRecipeSuccess,
} from './actions';
import { ActionRecipeItem } from '@backbase/actions-common-ang';
import { actionRecipeInitialState } from './initial-state';
import { ActionRecipesState } from '../model/actions-store-state.model';

const _actionRecipesReducer = createReducer(
  actionRecipeInitialState,
  on(loadActionRecipes, state => ({
    ...state,
    loading: true,
    error: undefined,
    specificationIds: undefined,
  })),
  on(loadActionRecipesSuccess, (state, action) => ({
    ...state,
    data: action.actionRecipes,
    loading: false,
    specificationIds: action.specificationIds,
  })),
  on(loadActionRecipesFailed, (state, action) => ({
    ...state,
    error: action.error,
    loading: false,
  })),
  on(saveActionRecipeSuccess, (state, action) => {
    const { actionRecipe: recipe } = action;
    const { data } = state;
    const targetIndex = data.findIndex(({ specificationId }) => specificationId === recipe.specificationId);
    const isAppend = targetIndex === -1;

    return {
      ...state,
      data: isAppend ? [...data, recipe] : insertActionRecipe(data, recipe, targetIndex),
    };
  }),
  on(saveActionRecipeFailure, (state, action) => {
    const { actionRecipe: recipe } = action;
    const { data } = state;
    const targetIndex = data.findIndex(({ specificationId }) => specificationId === recipe.specificationId);
    const isAppend = targetIndex === -1;

    return {
      ...state,
      data: isAppend
        ? [...data, { ...recipe, active: false }]
        : insertActionRecipe(data, { ...data[targetIndex] }, targetIndex),
    };
  }),
);

export function actionRecipesReducer(state: ActionRecipesState | undefined, action: Action): ActionRecipesState {
  return _actionRecipesReducer(state, action);
}

const insertActionRecipe = (data: ActionRecipeItem[], actionRecipe: ActionRecipeItem, index: number) => [
  ...data.slice(0, index),
  actionRecipe,
  ...data.slice(index + 1),
];
