import { createSelector } from '@ngrx/store';
import { mergeSpecificationsWithActionRecipes, SpecificationsWithAction } from '@backbase/actions-common-ang';
import { actionRecipes } from './action-recipes/selectors';
import { specifications } from './specifications/selectors';

export const specificationsWithActionRecipe = createSelector(
  specifications,
  actionRecipes,
  (specs, recipes): SpecificationsWithAction => mergeSpecificationsWithActionRecipes(specs, recipes),
);
