import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';

import { ActionsRecipesEffects } from './action-recipes/effects';
import { ActionsCommonModule } from '@backbase/actions-common-ang';
import { ActionsSpecificationsEffects } from './specifications/effects';
import { actionRecipesReducer } from './action-recipes/reducers';
import { specificationsReducer } from './specifications/reducers';

@NgModule({
  providers: [ActionsRecipesEffects, ActionsSpecificationsEffects],
  imports: [
    CommonModule,
    ActionsCommonModule,
    StoreModule.forFeature('actionRecipes', actionRecipesReducer),
    StoreModule.forFeature('specifications', specificationsReducer),
    EffectsModule.forFeature([ActionsRecipesEffects, ActionsSpecificationsEffects]),
  ],
})
export class ActionsStoreModule {}
