import { ActionRecipeGetResponseBody } from '@backbase/data-ang/actions';
import { createAction, props } from '@ngrx/store';
import { ActionRecipeItem, Notification } from '@backbase/actions-common-ang';
import { LoadActionRecipesPayload, SaveActionRecipePayload } from '../model/action-recipes-actions.model';

export const loadActionRecipes = createAction(
  '[ACT Action Recipes] Load Action Recipes',
  props<LoadActionRecipesPayload>(),
);

export const loadActionRecipesSuccess = createAction(
  '[ACT Action Recipes] Load Action Recipes Success',
  props<{
    actionRecipes: ActionRecipeGetResponseBody[];
    specificationIds?: Set<string>;
  }>(),
);

export const loadActionRecipesFailed = createAction(
  '[ACT Action Recipes] Load Action Recipes Failed',
  props<{ error?: Error }>(),
);

export const saveActionRecipe = createAction(
  '[ACT Action Recipes] Save Action Recipe',
  props<SaveActionRecipePayload>(),
);

export const saveActionRecipeSuccess = createAction(
  '[ACT Action Recipes] Save Action Recipe Success',
  props<{
    actionRecipe: ActionRecipeGetResponseBody;
    notification?: Notification;
  }>(),
);

export const saveActionRecipeFailure = createAction(
  '[ACT Action Recipes] Save Action Recipe Failure',
  props<{
    actionRecipe: ActionRecipeItem;
    notification?: Notification;
  }>(),
);
