import { Injectable } from '@angular/core';
import { NotificationService } from '@backbase/ui-ang/notification';
import { Store } from '@ngrx/store';
import { merge, of, throwError } from 'rxjs';
import { catchError, first, groupBy, map, mergeMap, switchMap } from 'rxjs/operators';
import {
  EMPTY_RESPONSE_ERROR,
  parseActionRecipeFormItem,
  getMappedRecurrenceResponse,
  NotificationsPreferencesBaseDataService,
} from '@backbase/actions-common-ang';
import {
  loadActionRecipes,
  loadActionRecipesFailed,
  loadActionRecipesSuccess,
  saveActionRecipe,
  saveActionRecipeFailure,
  saveActionRecipeSuccess,
} from './actions';
import { actionRecipesSpecificationIds } from './selectors';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { ActionsState } from '../model/actions-store-state.model';

@Injectable()
export class ActionsRecipesEffects {
  constructor(
    private readonly actions$: Actions,
    private readonly store: Store<ActionsState>,
    private readonly dataService: NotificationsPreferencesBaseDataService,
    private readonly notificationService: NotificationService,
  ) {}

  actionRecipes$ = createEffect(() =>
    this.actions$.pipe(
      ofType(loadActionRecipes),
      switchMap(({ specificationIds, arrangementId }) =>
        this.dataService.getActionRecipes(arrangementId).pipe(
          map(data => (specificationIds ? data.filter(({ specificationId: id }) => specificationIds.has(id)) : data)),
          map(data =>
            arrangementId ? data.filter(item => !item.arrangementId || item.arrangementId === arrangementId) : data,
          ),
          map(data =>
            loadActionRecipesSuccess({
              actionRecipes: data || [],
              specificationIds,
            }),
          ),
          catchError(error => of(loadActionRecipesFailed({ error }))),
        ),
      ),
    ),
  );

  saveActionRecipe$ = createEffect(() =>
    this.actions$.pipe(
      ofType(saveActionRecipe),
      groupBy(({ actionRecipe }) => actionRecipe.specificationId),
      mergeMap(group =>
        group.pipe(
          switchMap(({ actionRecipe, arrangementId, warningNotification, successNotification }) =>
            this.dataService.saveActionRecipe(actionRecipe, arrangementId).pipe(
              switchMap(({ actionRecipes }) => {
                if (!actionRecipes || !actionRecipes.length) return throwError(EMPTY_RESPONSE_ERROR);

                return of(
                  saveActionRecipeSuccess({
                    actionRecipe: {
                      ...actionRecipes[0],
                      ...(actionRecipes[0].recurrence && {
                        recurrence: getMappedRecurrenceResponse(actionRecipes[0].recurrence, actionRecipe),
                      }),
                    },
                    ...(successNotification && { notification: successNotification }),
                  }),
                );
              }),
              catchError(err => {
                if (err.status === 409) {
                  return this.store.select(actionRecipesSpecificationIds).pipe(
                    first(),
                    map(specificationIds =>
                      loadActionRecipes({
                        arrangementId,
                        specificationIds,
                      }),
                    ),
                  );
                }

                return of(
                  saveActionRecipeFailure({
                    actionRecipe: parseActionRecipeFormItem(actionRecipe),
                    ...(warningNotification && { notification: warningNotification }),
                  }),
                );
              }),
            ),
          ),
        ),
      ),
    ),
  );

  showNotification$ = createEffect(
    () =>
      merge(
        this.actions$.pipe(ofType(saveActionRecipeSuccess)),
        this.actions$.pipe(ofType(saveActionRecipeFailure)),
      ).pipe(
        map(({ notification }) => {
          if (notification) {
            this.notificationService.showNotification(notification);
          }
        }),
      ),
    { dispatch: false },
  );
}
