import { Injectable } from '@angular/core';
import { of } from 'rxjs';
import { catchError, map, switchMap } from 'rxjs/operators';
import { loadSpecifications, loadSpecificationsFailed, loadSpecificationsSuccess } from './actions';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { NotificationsPreferencesBaseDataService } from '@backbase/actions-common-ang';

@Injectable()
export class ActionsSpecificationsEffects {
  constructor(
    private readonly actions$: Actions,
    private readonly dataService: NotificationsPreferencesBaseDataService,
  ) {}

  specifications$ = createEffect(() =>
    this.actions$.pipe(
      ofType(loadSpecifications),
      switchMap(({ specificationIds }) =>
        this.dataService.getSpecifications().pipe(
          map(data => (specificationIds ? data.filter(({ id }) => !!id && specificationIds.has(id)) : data)),
          map(data => loadSpecificationsSuccess({ specifications: data || [] })),
          catchError(() => of(loadSpecificationsFailed({ error: true }))),
        ),
      ),
    ),
  );
}
