import { ActionRecipeItem } from '@backbase/actions-common-ang';
import { createFeatureSelector, createSelector } from '@ngrx/store';
import { ActionRecipesState, ActionsState } from '../model/actions-store-state.model';

export const selectActionRecipesState = createFeatureSelector<ActionsState, ActionRecipesState>('actionRecipes');

export const actionRecipes = createSelector(selectActionRecipesState, (state): ActionRecipeItem[] => state.data);

export const actionRecipesLoading = createSelector(selectActionRecipesState, state => state.loading);

export const actionRecipesError = createSelector(selectActionRecipesState, state => state.error);

export const actionRecipesSpecificationIds = createSelector(selectActionRecipesState, state => state.specificationIds);
