import { Action, createReducer, on } from '@ngrx/store';
import { loadSpecifications, loadSpecificationsFailed, loadSpecificationsSuccess } from './actions';
import { initialState } from '../initial-state';
import { SpecificationsState } from '../model/actions-store-state.model';

export const _specificationsReducer = createReducer(
  initialState.specifications,
  on(loadSpecifications, state => ({
    ...state,
    loading: true,
    error: false,
  })),
  on(loadSpecificationsSuccess, (state, action) => ({
    ...state,
    data: action.specifications,
    loading: false,
  })),
  on(loadSpecificationsFailed, (state, action) => ({
    ...state,
    error: action.error,
    loading: false,
  })),
);

export function specificationsReducer(state: SpecificationsState | undefined, action: Action): SpecificationsState {
  return _specificationsReducer(state, action);
}
