import { createFeatureSelector, createSelector } from '@ngrx/store';
import { ActionsState, SpecificationsState } from '../model/actions-store-state.model';

export const selectSpecificationsState = createFeatureSelector<ActionsState, SpecificationsState>('specifications');

export const specifications = createSelector(selectSpecificationsState, state => state.data);

export const specificationsLoading = createSelector(selectSpecificationsState, state => state.loading);

export const specificationsError = createSelector(selectSpecificationsState, state => state.error);
